/* login */
.login-form-container{
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 90%;
  background-color: #FFF;
  border-radius: 10px;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
}

.login-form-div{
  display: flex;
  justify-content: center;
  /* height: 80VH; */
}

.login-divider{
  margin-top: 20px;
  margin-bottom: 10px;
}
/* Homepage */
#homepage-h1{
  margin: 10px;
}
/* @media only screen and (max-width: 330px) {
  #homepage-h1{
    font-size: 12px;
  }
} */


@media only screen and (max-width: 330px){
#homepage-h1 {
    font-size: 25px;
    margin-bottom: 20px;
    margin-top: 20px;
  }
}

.homepage-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.homepage-link{
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.btn-circle.btn-xl {
  width: 100px;
  height: 100px;
  padding: 10px 16px;
  font-size: medium;
  font-weight: bold;
  line-height: 1.33;
  border-radius: 50%;
  margin: 5px;
}

@media only screen and (max-width: 330px){
.btn-circle.btn-xl {
  width:95px;
  height:95px;
  }
}

/* PROFILE */
.profile-form-div{
  display: flex;
  justify-content: center;
}

.profile-form-container{
  width: 80%;
}

h1{
  text-align: center;

}

.ins-card{
  width: 220px;
  border-radius: 10px;
}

#ins-card-container{
  display: flex;
  flex-direction: column;
}

.edit-profile-button{
  font-size: 16px;
}

input{
  border-radius: 10px;
  border: 1px solid #353A3F;
  box-shadow: 0 1px 1px 0;
}

input[type="text"]:disabled {
  border-radius: 10px;
  background: #FFF;

}

input[type="text"]:selection {
  border: 1px solid blue;
  border-radius: 10px;
}

/* RECORDS */
.record-form-div{
  display: flex;
  justify-content: center;
}

ul{
  list-style-type: none;
  padding: 0px;
  margin: 0px;
}

.li-record{
  border: solid black 1px;
  width: 300px;
  text-align: center;
  margin: 10px;
}

.appt-img{
  width: 220px;
}

.appt-filter-container{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

}

.category-button{
  margin: 2px;
}


/* Add Record- modal */
.addrecord-button-container{
  display: flex;
  justify-content: flex-end;
  margin: 20px;
}

/* Record Detail */
.record-detail-buttons{
  /* display: flex; */
}

.record-detail-button{
  margin: 2px;
}

/* Find Care */
.map{
  max-width: 200px;
  max-height: 200px;
}


.findcare-container{
  display: flex;
}

.findcare-locationsList-container{
  display: flex;

}
